	module.exports = {"data":{"post":{"html":"<p>I'm happy. <a href=\"http://www.veseg.de/\">SEG</a> has released new firmware for my Hollywood II DVD player that makes the region code setting configurable. I can now watch those DVDs I picked up in the States on my last couple of trips. Mama will be overjoyed (actually not; she and I don't have the same taste in movies).</p>\n<p><b>Update:</b> If you can read German, <a href=\"http://www.dvdboard.de/forum/showthread.php?postid=228883#post228883\">this entry</a> at dvdboard.de gives the details on changing the region setting.</p>","fields":{"title":"Happy Region Code Day","date":"26 May 2002","url":"https://papascott.de/2002-05-26-happy-region-code-day/"}}},"pathContext":{"slug":"/2002-05-26-happy-region-code-day/","prev":{"fields":{"title":"Grand Prix Night","date":"25 May 2002","slug":"/2002-05-25-grand-prix-night/"}},"next":{"fields":{"title":"Royal MT Hacks","date":"26 May 2002","slug":"/2002-05-26-royal-mt-hacks/"}}}}

